.app {
  margin: 0;
  background-color: #FFF;
  font-family: sans-serif;
  color: #262626;
}

.app__content {
  margin: 0 auto;
  min-width: 375px;
  max-width: 1440px;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  box-sizing: border-box;
  position: relative;
}

::-webkit-scrollbar {
  width: 5px;
  background-color: inherit;
}

::-webkit-scrollbar-thumb {
  background-color: #BCBCBC;
  border-radius: 10px;
  min-height: 169px;
}
